*, :before, :after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  width: 100%;
  height: 100%;
  display: block;
}

button {
  cursor: pointer;
  border: none;
  font-family: inherit;
}

.visually-hidden {
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  display: none;
}

.loader {
  height: 50px;
  width: 50px;
  margin: 50px auto;
  animation: rotate 1s infinite;
}

.loader:before, .loader:after {
  content: "";
  height: 20px;
  width: 20px;
  border-radius: 50%;
  display: block;
}

.loader:before {
  background-color: #fff;
  margin-bottom: 10px;
  animation: ball1 1s infinite;
  box-shadow: 30px 0 #ff3d00;
}

.loader:after {
  background-color: #ff3d00;
  animation: ball2 1s infinite;
  box-shadow: 30px 0 #fff;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg)scale(.8);
  }

  50% {
    transform: rotate(360deg)scale(1.2);
  }

  100% {
    transform: rotate(720deg)scale(.8);
  }
}

@keyframes ball1 {
  0% {
    box-shadow: 30px 0 #ff3d00;
  }

  50% {
    margin-bottom: 0;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #ff3d00;
  }

  100% {
    margin-bottom: 10px;
    box-shadow: 30px 0 #ff3d00;
  }
}

@keyframes ball2 {
  0% {
    box-shadow: 30px 0 #fff;
  }

  50% {
    margin-top: -20px;
    transform: translate(15px, 15px);
    box-shadow: 0 0 #fff;
  }

  100% {
    margin-top: 0;
    box-shadow: 30px 0 #fff;
  }
}

.container {
  margin: 15px auto;
  padding: 0 20px;
}

.cat-info {
  margin: 15px auto;
}

.ss-main {
  width: 20%;
  max-width: 230px;
  min-width: 170px;
}

.cat-info-container {
  max-width: 375px;
  background-color: #eee9e9;
  border-radius: 5px;
  padding: 15px;
  box-shadow: 2px 4px 4px rgba(110, 110, 110, .25);
}

.cat-info-thumb {
  height: 300px;
  width: 300px;
}

.cat-info-img {
  object-fit: contain;
  object-position: center;
}

.cat-info-title, .cat-info-desc {
  margin-bottom: 10px;
}

.cat-info-temp-span {
  font-weight: 600;
}

@media screen and (max-width: 767px) {
  .cat-info-thumb {
    margin: 10px auto;
  }
}

@media screen and (min-width: 768px) {
  .cat-info {
    justify-content: flex-start;
    align-items: center;
    gap: 20px;
    display: flex;
  }

  .cat-info-thumb {
    height: 350px;
    width: 450px;
  }

  .cat-info-container {
    max-width: 400px;
  }
}

@media screen and (min-width: 1200px) {
  .cat-info-container {
    max-width: 700px;
  }
}

